<template>
	<div class="submitButton">
		<svg width="154" height="54" xmlns="http://www.w3.org/2000/svg">
			<g id="button">
				<rect x="3" y="2" width="150" height="50" ry="25" class="rect">
					<!-- click -->
					<animate id="click" attributeName="width" attributeType="XML" from="150" to="50" begin="button.click" dur="0.3s" fill="freeze" restart="never"/>
					<animate attributeName="x" attributeType="XML" from="3" to="50" begin="button.click" dur="0.3s" fill="freeze" restart="never"/>
					<animate attributeName="stroke" attributeType="XML" from="#19CC94" to="gray" begin="button.click" dur="0.3s" fill="freeze" restart="never"/>
					<animate attributeName="fill" attributeType="XML" from="white" to="white" begin="button.click" dur="0.3s" fill="freeze" restart="never"/>
					<!-- loadend -->
					<animate id="ok" attributeName="width" attributeType="XML" from="50" to="150" begin="load.end" dur="0.3s" fill="freeze"/>
					<animate attributeName="x" attributeType="XML" from="50" to="3" begin="load.end" dur="0.3s" fill="freeze"/>
					<animate attributeName="stroke" attributeType="XML" from="gray" to="#19CC94" begin="load.end" dur="0.01s" fill="freeze"/>
					<animate attributeName="fill" attributeType="XML" from="white" to="#19CC94" begin="load.end" dur="0.01s" fill="freeze"/>
				</rect>

				<text x="55" y="32" class="text">
				Submit
					<animate attributeName="stroke-opacity" attributeType="XML" from="1" to="0" begin="button.click" dur="0.3s" fill="freeze" restart="never"/>
				</text>
			</g>

			<path d="M75,2,A25,25,0,1,1,74.9,2" id="circle">
				<animate id="load" attributeName="stroke-dashoffset" attributeType="XML" from="200" to="0" begin="click.end" dur="1s" fill="freeze"/>
				<animate attributeName="stroke-opacity" attributeType="XML" from="1" to="0" begin="load.end" dur="0.01s" fill="freeze"/>
			</path>

			<polyline points="60 25, 75 40, 100 15" class="arrow">
				<animate attributeName="stroke-dashoffset" attributeType="XML" from="200" to="0" begin="ok.end" dur="0.5s" fill="freeze"/>
			</polyline>
		</svg>
	</div>
</template>

<script>
export default {

  name: 'submitButton',

  data () {
    return {
    	
    };
  }
};
</script>

<style lang="scss" scoped>
path{
	stroke-dasharray: 200;
	stroke-dashoffset: 200;
}

svg{
	fill:none;

	&:hover{
		.rect{
			fill:#19CC94;
		}

		.text{
			stroke:white;
		}
	}

	#circle{
		stroke-width:3;
		stroke:#19CC94;
	}

	.rect{
		cursor:pointer;
		stroke:#19CC94;
		stroke-width:3;
	}

	.text{
		cursor:pointer;
		stroke:#19CC94;
	}
	.arrow{
		stroke:white;
		stroke-width:3;
		stroke-dasharray: 200;
		stroke-dashoffset: 200;
	}
}
</style>


